
.CircleContainer{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.CircleContainer .Circle{
    height: 60%;
    /*width: 100px;*/
    margin: 0 3px;

    aspect-ratio: 1/1;
    /*padding-bottom: 1%;*/
    border-radius: 50%;
    /*display: inline-block;*/
}
.CircleContainer .Red{
    background-color: red;
}
.CircleContainer .Yellow{
    background-color: yellow;
}
.CircleContainer .Green{
    background-color: green;
}