.AboutMe{
    min-height: 35rem;
    /*height: 100%;*/
    width: 100%;
    display: flex;
    flex-direction: column;
    /*margin: 3rem 0;*/
    /*padding: 2rem;*/
}

.AboutMeLogoContainer{
    flex: 1;
    display: flex;
    /*background-color: red;*/

    justify-content: center;
    align-items: flex-end;
}

.AboutMeLogoContainer svg{
    font-size: 4rem;
    margin: 0 0.4rem 0;
}

.AboutMeLogoContainer .ant-btn {
    padding: 0;
    margin: 0;
    /*height: 100%;*/
    border: 0;
    /*color:transparent;*/
    background-color:transparent;
    box-shadow: none;
    height: auto;
}


.AboutMeLogoContainer .ant-btn:not(:disabled):not(.ant-btn-disabled):hover,
.AboutMeLogoContainer .ant-btn:not(:disabled):not(.ant-btn-disabled):active {
    color: #353C46;
    /*background-color:lightgray;*/
}

@media (max-width: 570px){
    .AboutMeLogoContainer svg{
        font-size: 3rem;
    }
    .AboutMe{
        min-height: 36rem;
    }
}
@media (max-width: 480px){
    .AboutMeLogoContainer svg{
        font-size: 3rem;
    }
    .AboutMe{
        min-height: 44rem;
    }
}
@media (max-width: 400px){
    .AboutMeLogoContainer svg{
        font-size: 2rem;
    }
    .AboutMe{
        min-height: 44rem;
    }
}



