.Container{
    color: lightgray;
}

.Folder{
    font-weight: bold;
    margin: 0.2rem 0;
}

.Components{
    display: flex;
    margin: 0.1rem 0;
    overflow: hidden;
}

.FolderOpenLogo{
    margin: 0 5px;
}
.ant-layout .ant-layout-sider-zero-width-trigger {
    top:2rem;
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link-title{
    /*background-color: red;*/
    padding-left: 1.2rem;
    /*margin: 0.1rem 0;*/
    margin-top: 0.2rem;
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link-title:hover{
    background-color: rgba(102,102,102,0.63);
    /*margin: 0 0 10px 0;*/
    padding: 2px 0 2px 2rem;
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link-active>.ant-anchor-link-title {
    color: lightgray;
    background-color: #555555;
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link:last-child .ant-anchor-link-title:hover .RightArrow{
    color: #ff6666;
    /*transition: color 0.5s ease-in-out;*/
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link:last-child.ant-anchor-link-active > .ant-anchor-link-title{
    background-color: inherit;
    /*transition: color 0.5s ease-in-out;*/
}

.ant-anchor-wrapper .ant-anchor .ant-anchor-link:last-child.ant-anchor-link-active > .ant-anchor-link-title:hover{
    background-color: #555555;
    /*transition: color 0.5s ease-in-out;*/
}




.ant-layout .ant-layout-sider-zero-width-trigger::after {
    border-start-start-radius: 0;
    border-start-end-radius: 6px;
    border-end-end-radius: 6px;
    border-end-start-radius: 0;
}
