.ModalMaskContainer{
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.ModalMaskContainer .title,
.ModalMaskContainer .description {
    width: 100%
}

.ModalMaskContainer .title > h3{
    font-size: 2.4vw;
    font-weight: 660;
    margin: 0;
    /*padding: 1rem;*/
    word-break: break-all;
    word-wrap: break-word;
}

.ModalMaskContainer .description > p{
    margin: 0.4rem 0;
    font-size: 1.5vw;
    word-break: break-all;
    word-wrap: break-word;
    /*font-weight: 660;*/
}


.MyModalButtonContainer {
    margin: 1rem 0.5rem;
}

.MyModalButtonContainer .ant-btn .ant-image img{
    width: 23vw;
    height: 14vw;
    object-fit: cover;
    border-radius: 10px;
}

.MyModalButtonContainer .ant-btn{
    padding: 0;
    border: 0;

    color:transparent;
    background-color:transparent;
    box-shadow: none;
    height: auto;
}

.MyModalButtonContainer .ant-btn-primary:not(:disabled):not(.ant-btn-disabled):hover {
    color:transparent;
    background-color:transparent;
}
.MyModalButtonContainer .ant-btn-primary:not(:disabled):not(.ant-btn-disabled):active {
    color:transparent;
    background-color:transparent;
}

.ant-modal .ant-modal-content {
    background-color: #343b45;
    /*max-width: 30rem;*/
    /*width: 1000px;*/
    /*min-width: ;*/
}

.ant-modal .ant-modal-header{
    background-color: #343b45;
    width: 100%;
}


