.InsideModalButtonContainer{
    /*height: 100%;*/
    display: flex;
    justify-content: flex-end;
}

.InsideModalButtonContainer .ant-btn {
    padding: 0.2rem 0.6rem;
    margin: 0 0 0 0.4rem;
    height: 100%;
    /*border: 0;*/
}
.InsideModalButtonContainer .ant-btn > span{
    /*margin: 0.4rem;*/
    /*border: 0;*/
}

@media (max-width: 650px){
    .MyModalButtonContainer .ant-btn .ant-image img{
        /*width: 80vw;*/
        /*height: 40vw;*/
        width: 100%;
        height: 100%;
    }
    .ModalMaskContainer .title > h3{
        font-size: 6vw;
        margin: 0;

    }

    .ModalMaskContainer .description > p{
        font-size: 3vw;
    }
}

@media (max-width: 350px){
    .InsideModalButtonContainer{
        /*height: 100%;*/
        display: flex;
        justify-content: center;
    }
    .InsideModalButtonContainer .ant-btn {
        margin: 0 0 0 0.4rem;
        width: 30vw;
        height: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .InsideModalButtonContainer .ant-btn > span{
        font-size: 3vw;
    }
}