/*Container Stuff*/

.ContentContainer{
    max-height: 100%;
    overflow-y: scroll;
    /*font-size: 2vw;*/
    color: lightgray;
    /*padding: 20px 0;*/

}

.TabsContainer{
    background-color: #4F565E;
    height: 30px;
    display: flex;
    padding-top: 3px;
}

.Content{
    padding: 10px 20px 30px;
    color: rgb(255, 255, 255, 0.15);
}

.TextGrey{
    color: lightgray;
}

.TextGreen{
    color: rgba(158,255,35,0.7);
}

/*The Tabs Thing*/
.Symbols{
    min-width: 50px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Tab1{
    background-color: #353C46;
    display: flex;
    width: 200px;
    height: 100%;
    border-radius: 5px 5px 0 0;
    align-items: center;
    justify-content: center;
}

.Tab1 > span {
    flex: 1;
    display: flex;
    align-items: center;
    text-align: left;
    padding-left: 20px;
    /*color: rgb(255, 255, 255, 0.15);*/
    color: lightgray;
}
.Tab1 .CloseSymbol{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

/*Div Component Stuff*/

.ant-collapse{
    background-color: #353C46;
}

.ant-collapse-header{
    margin: 0 0 0 0;
    background-color: #353C46;
    /*padding: 20px;*/
}

.ant-collapse>.ant-collapse-item >.ant-collapse-header {
    /*padding: 4px;*/
    padding: 0;
}

.ant-collapse-header:hover{
    border-radius: 0;
    background-color: rgba(102,102,102,0.63);
    margin: 0 0 0.8rem 0;
    /*transform: scale(105%);*/
    /*height: 2rem;*/
}

.ant-collapse>.ant-collapse-item:last-child,
.ant-collapse>.ant-collapse-item:last-child>.ant-collapse-header {
    border-radius:4px 4px 4px 4px;
}

.ant-collapse .ant-collapse-item:last-child >.ant-collapse-content {
    border-radius:4px 4px 4px 4px;
    /*margin: 0.8rem;*/
    margin-bottom: 0.8rem;
    /*padding: 20px;*/
}

.ant-collapse-item.ant-collapse-item-active .ant-collapse-header:first-child{
    margin: 0 0 0.8rem 0;
    /*transform: translateY(20px);*/
}
.ant-collapse-item.ant-collapse-item-active .ant-collapse-header:first-child:hover{
    margin: 0 0 1.6rem 0;
    /*transform: translateY(20px);*/
}


.ant-collapse .ant-collapse-content {
    background-color:rgb(255, 255, 255, 0.15);
}

