.EducationContainer{
    display: flex;
    min-height: 10rem;
}

.EducationContainer:not(:last-child){
    margin: 0 0 6rem 0;
}

.ImageContainer{
    margin-right: 10vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ImageContainer .ant-image{
    display: flex;
    align-items: center;
    justify-content: center;
}

.ImageContainer .ant-image > img{
    width: 20vw;
}

.ImageContainer .MaskContainer{
    border-radius: 10px;
}

.TextContainer{
    transform: translateY(-0.6rem);
    display: flex;
    /*flex-direction: ;*/
    align-items: center;
    justify-content: center;
}
