.TextContainer .MyText > h2 {
    font-size: 1.5rem;
    margin: 0;
    font-weight: 600;
}

.TextContainer .MyText > h3 {
    margin: 0 0 0.5rem;
    color: lightgray;
    font-weight: 600;
    font-size: 1rem;
}

.TextContainer .MyText > h5 {
    font-size: 1rem;
    color: rgb(211,211,211, 60%);
    margin: 0 0 0 0;
    font-weight: normal;
    /*padding: 0 0 2rem 0;*/
}

.TextContainer .MyText > br {
    margin: 0 0 1.5rem 0;
}
