/*}*/

@media (max-width: 650px){
    .EducationContainer{
        flex-direction: column;
    }
    .ImageContainer{
        margin: 3vw 0 7vw 0;
    }

    .EducationContainer:not(:last-child){
        margin: 0 0 2rem 0;
    }

    .TextContainer{
        display: block;
    }
    .ImageContainer .ant-image > img{
        width: 80%;
    }
    .ImageContainer{
